<template>
        <section class="main-content">
                 <loader :is-visible="isLoading"></loader>
                <div class="row" v-if="callFrom != 'Wizard'">
                    <div class="col-md-12 padding-t_8">
                        <div class="theme-primary partition-full">
                            <span class="p-name text-white"> {{ $t('ManageServiceAppointment') }}</span>
                            <span class="float-right">
                                <RouterLink to="/ServiceAppointment/index" class="p-action-btn text-white float-right">
                                    <em class="fa fa-angle-double-left"></em>
                                    <br />
                                    {{ $t('BacktoList') }}
                                </RouterLink>
                            </span>
                        </div>
                    </div>
                </div>
                <div class="border p-3">                   
                    <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="OnSubmit">
                        </dynamic-form>
                    <div class="row">                        
                        <div class="col-lg-6">
                            <div class="py-2">
                                <small class="text-danger">{{ $t('MandatoryString') }}</small>
                            </div>
                        </div>
                    </div>                    
                </div>
    </section>
</template>
<script>
import DataService from "@/services/DataService";
export default {
    data() {
        return {
            isLoading: false,
            StaticObj: {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "MultiSelectField",
                            label: this.$t('ServiceResource'),
                            name: "ServiceResource",
                            value: "",
                            mode: "tag",
                            placeholder: "",
                            validationRules: {
                                "required": false
                            },
                            config: {
                                options: []
                            },
                        },
                        {
                            astype: "MultiSelectField",
                            label: this.$t('Service Crew'),
                            name: "ServiceCrew",
                            value: "",
                            mode: "tag",
                            placeholder: "",
                            validationRules: {
                                "required": false
                            },
                            config: {
                                options: []
                            },
                        }
                    ]
            },
            FormSchema: [],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Submit"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ],
            resourceList: [],
            crewList: [],
            SAData: [],
            isFsmLocating: '',
            SAFormSchema: [],
            StatusId:'1001',
            StatusData:[],
            DurationData:[]
        }
    },
    created: async function () {
        await this.getForm();
       // await this.BindFieldValueDropDown();
       // await this.getResourceList();
       // await this.getcrewList();
    },
    methods: {       
        getResourceList: async function () {
            var url = `moduleName=&type=FSM_SA_RESOURCENAME&refId=`;
            await DataService.GetDDLData(url).then(response => {
                this.resourceList = response.data.DATA;
                this.resourceList.forEach(item => {
                    let obj = {
                        name: item.name,
                        value: item.value
                    };
                    this.FormSchema[2].Data[0].config.options.push(obj);
                });
            })
        },
        getcrewList: async function () {
            var url = `moduleName=&type=FSM_CREWNAME&refId=`;
            await DataService.GetDDLData(url).then(response => {
                this.crewList = response.data.DATA;
                this.crewList.forEach(item => {
                    let obj = {
                        name: item.name,
                        value: item.value
                    };
                    this.FormSchema[2].Data[1].config.options.push(obj);
                });
            })
        },
        async getForm() {
            var vm = this;
            var url = `moduleName=FSM&subModuleCode=SERVICE_APPOINTMENT&langCode=en`;
            await DataService.ManageServiceAppointment(url).then(response => {
                vm.SAData = response.data.data;
                vm.isFsmLocating = response.data.IS_FSM_LOCATING;
                if (vm.SAData != null && vm.SAData.length > 0) {
                    var groupId = vm.SAData[0].group_id;
                    $.each(vm.SAData, $.proxy(function (index, item) {
                        if (item.form_field_visibility == 'NO') {
                            return;
                        }
                        groupId = item.group_id;                        
                        if(item.name == 'Status'){                            
                            vm.StatusData=JSON.parse(item.select_options);
                        }
                        if(item.name=='SA_Duration_Type'){
                            var DurationType=item.picklist_options.split(',');;
                            DurationType.forEach(itm=>{                                
                                vm.DurationData.push({
                                    name:itm,
                                    value:itm
                                })
                            })
                        }
                        var chkGroup = $.grep(vm.SAFormSchema, function (element, index) {
                            return element.group_id == groupId;
                        });
                        if (chkGroup.length == 0) {
                            var returnedData = $.grep(vm.SAData, function (element, index) {
                                return element.group_id == groupId;
                            });
                            var finalData = [];
                            returnedData.forEach(item => {
                                if (item.form_field_visibility == 'YES') {
                                    let DataObj = {};                                    
                                    if (item.astype == 'TextField') {
                                        DataObj = {
                                            astype: item.astype,
                                            label: item.display_name,
                                            name: item.name,
                                            value: item.value,
                                            disabled: false,
                                            validationRules: { "required": item.system_is_required },
                                        }
                                        finalData.push(DataObj);
                                    }
                                    if (item.astype == 'NumericField') {
                                        DataObj = {
                                            astype: item.astype,
                                            label: item.display_name,
                                            name: item.name,
                                            value: item.value,
                                            disabled: false,
                                            validationRules: { "required": item.system_is_required },
                                        }
                                        finalData.push(DataObj);
                                    }
                                    if (item.astype == 'CustomSelectField') {
                                        DataObj = {
                                            astype: item.astype,
                                            label: item.display_name,
                                            name: item.name,
                                            value: item.value,
                                            disabled: false,
                                            config: {
                                                options:[]
                                                //options: JSON.parse(item.select_options)
                                            },
                                            validationRules: { "required": item.system_is_required },
                                        }
                                        finalData.push(DataObj);
                                    }
                                    if (item.astype == 'SelectField') {                                        
                                        DataObj = {
                                            astype: item.astype,
                                            label: item.display_name,
                                            name: item.name,
                                            value: item.value,
                                            disabled: false,
                                            config: {
                                                options:[]
                                                //options: JSON.parse(item.select_options)
                                            },
                                            validationRules: { "required": item.system_is_required },
                                        }
                                        finalData.push(DataObj);
                                    }
                                    if (item.astype == 'TextAreaField') {
                                        DataObj = {
                                            astype: item.astype,
                                            label: item.display_name,
                                            name: item.name,
                                            value: item.value,
                                            disabled: false,
                                            validationRules: { "required": item.system_is_required },
                                        }
                                        finalData.push(DataObj);
                                    }
                                    if (item.astype == 'DatetimeField') {
                                        DataObj = {
                                            astype: 'DateField',
                                            label: item.display_name,
                                            name: item.name,
                                            value: item.value,
                                            disabled: false,
                                            validationRules: { "required": item.system_is_required },
                                        }
                                        finalData.push(DataObj);
                                    }

                                }
                            })
                            if (returnedData.length > 0) {
                                var objGroup = {
                                    group_id: groupId,
                                    layoutType: "four",
                                    group_name: returnedData[0].group_name,
                                    group_display_name: returnedData[0].group_display_name,
                                    Data: finalData
                                };
                                vm.SAFormSchema.push(objGroup);
                            }
                        }
                    }, this));
                    vm.FormSchema = vm.SAFormSchema;
                    vm.FormSchema.push(vm.StaticObj);                                        
                }
            });
        },
        async BindFieldValueDropDown(){            
            this.FormSchema[0].Data[2].config.options=this.DurationData;
            this.FormSchema[0].Data[5].config.options=this.StatusData;
            this.FormSchema[0].Data[0].disabled=true;
            this.FormSchema[0].Data[1].disabled=true;
            this.FormSchema[0].Data[0].value=this.AddServiceAppointmentDetails.TicketId;
            this.FormSchema[0].Data[1].value=this.AddServiceAppointmentDetails.WorkOrderId;
        },
        Close(){
            this.$router.push({ name: 'ServiceAppointmentListing' });
        },
        OnSubmit() {
            
        }
    }
}
</script>